import { RM } from '@speedlo/tools'
import { LanguagePicker } from '@speedlo/ui'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { Colors } from '../../../styling'
import { useMenuModel } from '../hooks/useMenu'

interface IProps {}

const StyledLanguagePicker = styled(LanguagePicker)`
  transform: scale(2) translateY(0.5rem);
  transform-origin: right;
`

export const MenuLanguageSwitch = observer<IProps>(() => {
  const menuModel = useMenuModel()
  return (
    <StyledLanguagePicker
      availableLanguages={menuModel.supportedLanguages}
      selectedLanguage={menuModel.currentLanguage}
      onChange={menuModel.setLanguage}
      formatLanguage={RM.identity}
      arrowColor={Colors.greyMiddle}
    />
  )
})
